import React, { Component } from 'react'
import Input from './Input.js';
import List from './List.js';

export default class TodoList extends Component {//rcc
    constructor(){
        super();
        this.state={
            inpValue:'',
            todos:[
               
            ]
        }
    }
    handlChange=(e)=>{
        this.setState({inpValue:e.target.value})
    }

    
    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
    addTodo=()=>{
        if(this.state.inpValue != ''){
            this.setState({
                todos:[
                    {
                        title:this.state.inpValue,
                        
                        done:false
                    },
                    
                    ...this.state.todos
                ]
                
            })
        }
        this.setState({inpValue:''})
    }
    delTodo=(idx)=>{
        // let newTodos = JSON.parse(JSON.stringify(this.state.todos));
        // newTodos.splice(idx,1);
        // this.setState({todos:newTodos})
        this.setState({
            todos:this.state.todos.filter((item,index)=>index != idx)
        })
        // ()=>{
        //     localStorage.setItem(
        //         'todos',
        //         JSON.stringify(this.state.todos)
        //     )
        // }
    }

    toggle=(idx)=>{
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));
        newTodos[idx].done = !newTodos[idx].done;
        this.setState({
            todos:newTodos
        })
    }
    renderTodos=(done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
                return <li>
                        <input onClick={()=>this.toggle(idx)} type="checkbox" checked={done}/>
                        <span>{todo.title}</span>
                        <button onClick={()=>this.delTodo(idx)}>删除</button>
                    </li>
            }
        })
    } 
    render() {
        const {todos,inpValue}=this.state;
        return (
            <div className="box">
                <Input 
                    inpValue={inpValue} 
                    handlChange={this.handlChange} 
                    addTodo={this.addTodo} />
                <List 
                    todos={todos} 
                    renderTodos={this.renderTodos} />
            </div>
        )
    }
}